import React, { Component, Fragment } from 'react';

import {
  Card,
  Row,
  Table,
  Popconfirm,
  Col,
  Menu,
  Icon,
  Cascader,
  Select,
  Checkbox,
  Input,
} from 'antd';

import Page from 'components/LayoutComponents/Page';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { arrangeGV } from 'dump/data';
import './style.scss';
import SearchBarTrainingPlan from './FormTrainingPlan';

class TrainingPlanTable extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    const columns = [
      {
        title: 'Tên lớp',
        dataIndex: 'className',
      },
      {
        title: 'Tên môn',
        dataIndex: 'name',
      },
      {
        title: 'Số học trình',
        dataIndex: 'SHT',
      },
      {
        title: 'Lớp ghép',
        dataIndex: 'classG',
      },
      {
        title: 'Loại giờ',
        dataIndex: 'LG',
      },
      {
        title: 'Tốp thứ',
        dataIndex: 'TT',
      },
      {
        title: 'S tiết CTĐT',
        dataIndex: 'ST',
      },
      {
        title: 'S tiết tuần',
        // dataIndex: 'STT',
        render: (text, record) => <Input style={{ width: '75px' }} value={record.STT} />,
      },
      {
        title: 'Từ tiết mấy',
        // dataIndex: 'TTM',
        render: (text, record) => <Input style={{ width: '75px' }} value={record.TTM} />,
      },
      {
        title: 'Đến tiết mấy',
        // dataIndex: 'DTM',
        render: (text, record) => <Input style={{ width: '75px' }} value={record.DTM} />,
      },
      {
        title: 'S tiết xếp KH',
        dataIndex: 'STXKH',
      },
      {
        title: 'S tiết chưa xếp KH',
        dataIndex: 'STCX',
      },
      {
        title: 'S tiết xếp TKB',
        dataIndex: 'STXTKB',
      },
      {
        title: 'Ca học',
        dataIndex: 'CH',
      },
      {
        title: 'Giai đoạn',
        dataIndex: 'GD',
      },
      {
        title: 'Từ tuần',
        dataIndex: 'TTT',
      },
      {
        title: 'Đến tuần',
        dataIndex: 'DT',
      },
      {
        title: 'Từ ngày',
        dataIndex: 'date',
      },
    ];
    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
    };
    return (
      <Card title="Phân bổ giáo viên, ca học, phòng học">
        <Row>
          <Col span={24}>
            <SearchBarTrainingPlan />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              scroll={{ x: 1500 }}
              rowSelection={rowSelection}
              bordered
              dataSource={arrangeGV}
              columns={columns}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <TrainingPlanTable />
  </Page>
);



