import React, { Component } from 'react';
import { Row, Col, Form, Select, Input } from 'antd';
import { BtnSearch, BtnClear, BtnSave } from 'components/common/button';
const { Option } = Select;

@Form.create({ name: 'trainingPlan' })
class SearchBarTrainingPlan extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="ant-advanced-search-form" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={8}>
            <Form.Item label="Hệ">
              {getFieldDecorator('system1', {})(
                <Select>
                  <Option value="1">Đại học chính quy</Option>
                  <Option value="2">Cao đẳng chính quy</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Khoa">
              {getFieldDecorator('system2', {})(
                <Select>
                  <Option value="1">Cơ-điện tử</Option>
                  <Option value="2">Quản trị kinh doanh</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Khóa">
              {getFieldDecorator('system3', {})(
                <Select>
                  <Option value="1">Khóa 1</Option>
                  <Option value="2">Khóa 2</Option>
                  <Option value="3">Khóa 3</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="CN">
              {getFieldDecorator('system4', {})(
                <Select>
                  <Option value="1">Cơ khí</Option>
                  <Option value="2">Điện tử viễn thông</Option>
                  <Option value="3">Điện công nghiệp</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Lớp">
              {getFieldDecorator('system5', {})(
                <Select>
                  <Option value="1">KT1</Option>
                  <Option value="2">KT2</Option>
                  <Option value="3">KT3</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Học kỳ">
              {getFieldDecorator('system6', {})(
                <Select>
                  <Option value="1">Học kỳ 1</Option>
                  <Option value="2">Học kỳ 2</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Năm học">
              {getFieldDecorator('system7', {})(
                <Select>
                  <Option value="1">2017-2018</Option>
                  <Option value="2">2018-2019</Option>
                  <Option value="3">2019-2020</Option>
                  <Option value="3">2021-2022</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Ca học">
              {getFieldDecorator('system8', {})(
                <Select>
                  <Option value="1">Ca 1</Option>
                  <Option value="2">Ca 2</Option>
                  <Option value="3">Ca 3</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Bộ môn">
              {getFieldDecorator('system8', {})(
                <Select>
                  <Option value="1">Sức khỏe</Option>
                  <Option value="2">Giáo dục </Option>
                  <Option value="3">Y tế</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Loại giờ">
              {getFieldDecorator('system8', {})(
                <Select>
                  <Option value="1">1 giờ</Option>
                  <Option value="2">2 giờ </Option>
                  <Option value="3">3 giờ</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Giai đoạn">{getFieldDecorator('system8', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Từ tuần">{getFieldDecorator('system8', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Đến tuần">{getFieldDecorator('system8', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Học phần">{getFieldDecorator('system8', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Giáo viên">{getFieldDecorator('system8', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Phòng học">{getFieldDecorator('system8', {})(<Input />)}</Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSave style={{ marginRight: 0 }} />
              {/* <BtnClear /> */}
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBarTrainingPlan;



